body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(38, 77, 74);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

label {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.copytoclipboard {
  display: flex;
  width: 200px;
  height: 30px;
  background-color: rgba(23, 117, 28, 0.842);
  margin-left: auto;
  margin-right: auto;
  border-radius: 7px;
  z-index: 1;
  position: absolute;
  margin-top: -36px;
  left: 0;
  right: 0;
  text-align: center;
  align-items: center;
  font-size: large;
  justify-content: center;
  padding-top: 1px;
  padding-bottom: 1px;
  color: white;
  font-weight: bold;
  visibility: visible;
  animation: cubic-bezier(0.95, 0.05, 0.795, 0.035) fade 1.5s forwards;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}
.gameTitle {
  margin: auto;
  width: 200px;
  justify-content: center;
  text-align: center;
  color: #ffffffd2;
  font-size: 20px;
}
.myname {
  writing-mode: vertical-rl;
  text-orientation: mixed;
  display: flex;
  z-index: 3;
  position: absolute;
  top: 25px;
  left: -15px;
  color: #b3f5b370;
}
@keyframes fade {
  to {
    opacity: 0;
    z-index: -1;
    display: none;
  }
}

.winnerBanner {
  display: flex;
  width: 260px;
  height: 260px;
  background-color: rgb(192, 238, 195);
  margin-left: auto;
  margin-right: auto;
  border-radius: 7px;
  z-index: 1;
  position: absolute;
  margin-top: -266px;
  left: 0;
  right: 0;
  text-align: center;
  align-items: center;
  font-size: 40px;
  justify-content: center;
  padding-top: 1px;
  padding-bottom: 1px;
  color: rgb(35, 87, 0);
  font-weight: bold;
  visibility: visible;
  animation: cubic-bezier(0.95, 0.05, 0.795, 0.035) fade 2.5s forwards;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

input {
  height: 28px;
  margin-top: 6px;
  padding: 4px;
  border-style: hidden;
  border-radius: 7px;
  border: solid 2px rgba(14, 110, 56, 0.719);
  text-align: center;
  color: rgba(255, 255, 255, 0.842);
  background-color: rgba(46, 164, 79, 0.4);
  font-weight: bold;
}
input::placeholder {
  color: rgba(255, 255, 255, 0.829);
  background-color: rgba(46, 164, 79, 0);
  text-align: center;
}
.gameId {
  width: 200px;
  font-size: larger;
  display: flex;
  margin-top: 7px;
  margin-left: auto;
  margin-right: auto;
}

.board {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 270px;
  height: 270px;
  display: grid;
  grid-template-rows: 90px 90px 90px;
  grid-template-columns: 90px 90px 90px;
}
.valor {
  margin-left: 0px;
  margin-top: -2px;
  padding: 0;
  /*animation: vvalue 0.7s cubic-bezier(0.47, 0, 0.745, 0.715);*/
}
@keyframes vvalue {
  from {
    color: rgba(17, 202, 0, 0.863);
    opacity: 0;
    display: none;
  }
  to {
    display: inherit;
    opacity: 1;
    color: rgb(207, 207, 207);
  }
}
.cell {
  color: rgb(207, 207, 207);
  display: flex;
  align-items: center;
  border: 5px solid rgb(207, 207, 207);
  padding: 23px;
  font-size: 45px;
  font-weight: bold;
  border-radius: 20%;
  margin: 1px;
}
.cellX {
  color: rgb(207, 207, 207);
  display: flex;
  align-items: center;
  border: 5px solid rgb(207, 207, 207);
  padding: 23px;
  font-size: 45px;
  font-weight: bold;
  border-radius: 20%;
  margin: 1px;
  animation: example 0.7s cubic-bezier(0.47, 0, 0.745, 0.715);
}
.cellO {
  color: rgb(207, 207, 207);
  display: flex;
  align-items: center;
  border: 5px solid rgb(207, 207, 207);
  padding: 23px;
  font-size: 45px;
  font-weight: bold;
  border-radius: 20%;
  margin: 1px;
  animation: example 0.7s cubic-bezier(0.47, 0, 0.745, 0.715);
}

@keyframes example {
  0% {
    border: 5px solid rgb(207, 207, 207);
    color: rgb(207, 207, 207);
  }
  25% {
    border: 5px solid rgba(17, 202, 0, 0.863);
    color: rgba(17, 202, 0, 0.863);
  }
  100% {
    border: 5px solid rgb(207, 207, 207);
    color: rgb(207, 207, 207);
  }
}

.standard {
  align-items: center;
  justify-content: center;
  width: 135px;
  display: flex;
  margin-top: 6px;
  margin-left: auto;
  margin-right: auto;
  appearance: none;
  background-color: #2ea44f;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: 6px;
  box-shadow: rgba(27, 31, 35, 0.1) 0 1px 0;
  box-sizing: border-box;
  color: rgba(255, 255, 255, 0.952);
  cursor: pointer;
  font-family: -apple-system, system-ui, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 23px;
  font-weight: 600;
  line-height: 20px;
  padding: 10px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
}

.standard:focus:not(:focus-visible):not(.focus-visible) {
  box-shadow: none;
  outline: none;
}

.standard:hover {
  background-color: #2c974b;
}

.standard:focus {
  box-shadow: rgba(46, 164, 79, 0.4) 0 0 0 3px;
  outline: none;
}

.standard:disabled {
  background-color: #94d3a2;
  border-color: rgba(27, 31, 35, 0.1);
  color: rgba(255, 255, 255, 0.8);
  cursor: default;
}

.standard:active {
  background-color: #298e46;
  box-shadow: rgba(20, 70, 32, 0.2) 0 1px 0 inset;
}

